import style from '../style';
// <img src="../../assets/databox/mockupsalad.png" />
import { h, Component } from 'preact';

//put scroll margins in here! use refs!!

const images = [
    {
        id: "sdkvid",
        src: "../../assets/databox/sdkvid.png",
        video: "../../assets/databox/thesdk.mp4",
        description: "image: sdk video",
        overview: "I had a lot of fun making this.  I used keynote, which, though fairly limited, was good enough.  The headaches were with the timings.  A couple of seconds out on the last slide?  Adjust and re-run from the beginning to check..."
    },
    {
        id: "mockupsalad",
        src: "../../assets/databox/mockupsalad.png",
        description: "image: databox 'health app' mockup",
        overview: "A quick mockup of a possible databox app.  The idea was that databox would monitor shopping and exercise and recommend recipes based on calories consumed..."
  
    },
    {
        id: "personalviz",
        src: "../../assets/databox/personalviz.png",
        description: "image: data tracker' mockup`",
        overview: "The databox sdk injects information that can be used to track the flow of personal data.  This makes it possible to create a realtime overview of the personal data being processed.  This was a first version of a proof-of-concept app that would track the flows of all apps running on the databox. More detail can be found in the papers section"
  
    },
    {
        id: "mockupenergy",
        src: "../../assets/databox/mockupenergy.png",
        description: "image: databox 'energy app' mockup",
        overview: "This is another simple mockup of a potential databox app that monitors the consumption of household energy."
    },
    {
        id: "showcaseui",
        src: "../../assets/databox/showcaseui.png",
        description: "image: databox bt showcase app",
        overview: "This is a screen of an app that I showcased at a BT innovation event.  The bird moves across the screen, and when a tweet is received with hashtag databox, the bird is reset, and the tweet is analysed for sentiment, and tallied as positive, negative or neutral.  The viz was built entirely using my databox sdk, using an inbuilt tool that makes it easy to attach data to attributes of a vector image"
    },
    {
        id: "usecase3",
        src: "../../assets/databox/usecase3.png",
        description: `image: databox insurance usecase`,
        overview: "A usecase graphic shown at BT and and Mozilla Festival (Mozfest) to give some context the advantages and potential of databox apps.  This shows how sensitive data acan be processed locally to provide a bespoke insurance quote (i.e. the insurer need never access the customer's data)"
    },
    {
        id: "usecase2",
        src: "../../assets/databox/usecase2.png",
        description: `image: databox privad usecase`,
        overview: "Another usecase graphic shown at BT and Mozilla Festival (Mozfest) to give some context the advantages and potential of databox apps.  This shows how local data could be used to provide users with highly tailored adverts (again, without revealing personal data)"
    },
    {
        id: "usecase1",
        src: "../../assets/databox/usecase1.png",
        description: `image: databox fraud usecase`,
        overview: `Another usecase graphic shown at BT and Mozilla Festival (Mozfest) to give some context the advantages and potential of databox apps.  This presents a scenario where databox might be used to help prevent fraud.  A bank need only ask "is Henry in Thailand?" rather than "Where is Henry?", limiting the amount of information it needs to identify fraud.`
    },
    
];

export default class Databox extends Component {

    state = {
        active: null
    }

    selectItem = (item) => {
       
        const {id} = item || {};
        if (id !== this.state.active) {
            const left = this[`image_${id}`] ?  Math.max(35, (this.gallery.getBoundingClientRect().width - 750 - (200/2)) /2) : 0;
            const top  = this[`image_${id}`]  ? Math.max(120, (this.gallery.getBoundingClientRect().height - 400) /2): 0;
            this.setState({ active: id, left, top, item});
        } 
        else {
            this.setState({ active: null, left: 0, top:0, item:null });
        }

    };

    renderTab(){
        console.log("in render tab with state", this.state);
        return <div  onClick={() => { this.selectItem(null) }} class={style.tabtitle} style={{top: -40, left:0}}>
           {this.state.item.id}
        </div>
    }

    renderDescription(){

        const cstyle = {
            height: this.state.active==="sdkvid" ? 590: 530,
            left: 770,
            top: 0,
        }

        return <div  onClick={() => { this.selectItem(null) }} class={style.overview} style={cstyle}>
            {this.state.item.overview}
        
        </div>
    }

    renderActive(){
        return <div class={style.fullcontainer} style={{height: 530, width: 970, left:this.state.left+10, top: this.state.top+10}}>
         {this.renderDescription()}
         {this.renderTab()}
        </div>
       
    }

    renderItem(item){
        const imagestyle = this.state.active == item.id ? style.imageactive : style.image;
        const thumbstyle = this.state.active == item.id ? {position:'absolute', top: this.state.top, left: this.state.left, zIndex:2} : {};
        const containerstyle = {
            padding: this.state.active ? "10px" : "10px 10px 75% 10px",
            height: this.state.active ? "auto" : 0,
        }

        if (this.state.active==="sdkvid"){
           return <div key={item.id} ref={r => this[item.id] = r} onClick={() => { this.selectItem(item) }} class={style.thumbnail} style={thumbstyle}>
                <div class={style.imagecontainer} style={containerstyle}>
                    <video ref={r=>this.video=r} class={imagestyle} autoplay onclick={(e)=>{
                      e.target.pause()
                    }}>
                        <source  src={item.video} />
                    </video>
                </div>
                <div class={style.info}>
                    {!this.state.active && item.description}
                </div>
            </div>
        }


        return <div key={item.id} ref={r => this[item.id] = r} onClick={() => { this.selectItem(item) }} class={style.thumbnail} style={thumbstyle}>
                <div class={style.imagecontainer} style={containerstyle}>
                    <img ref={r=> this[`image_${item.id}`] = r} class={imagestyle} src={item.src} />
                </div>
                <div class={style.info}>
                    {!this.state.active && item.description}
                </div>
            </div>
    }

    render() {
        const items = images.map(i => this.renderItem(i));

        return (<div ref={r=>this.gallery=r} class={style.gallery}>
            {this.state.active && <div  onClick={() => { this.selectItem(null) }} class={style.opaque}/>}
            {this.state.active && this.renderActive()}
            {items}
        </div>)
    }
}





