import style from '../style';
const My = () => (
    <div> I have built a web-based IDE for building apps that run on a dedicated <strong>databox</strong> in a user's home.  The development environment is flow-based; data flows across edges to nodes: black boxes of functionality.  The IDE automatically tracks the flow of personal data and flags it to the developer.  The IDE also provided an in-browser testing environment, context sensitive help and static code checking. Once published, the IDE builds a docker image of the app ready for deployment.</div>
);

export default My;


