
import localstyle from './style';
import base from './base';

import { Link } from 'preact-router/match';
import { h, Component } from 'preact';
import ProjectPic from './projectpic';
import FooterPic from '../../components/footer/footerpic';
import Tech from './routes/tech';
import About from './routes/about';
import My from './routes/my';
import Papers from './routes/papers';
import Design from './routes/design';


const style = {...base, ...localstyle};

export default class Databox extends Component {

    renderContent(item = "about") {

        switch (item) {
            case "tech":
                return <Tech />;
            case "my":
                return <My />;
            case "papers":
                return <Papers />;
            case "design":
                return <Design />;
            default:
                return <About />;
        }
    }

    renderFooterImage(item = "about") {
        switch (item) {
            case "design":
                return null;
            default:
                return <div class={style.footergraphic}>
                    <FooterPic />
                </div>
        }
    }

    renderLinks(item = "about") {
        return null;
        console.log(item);
        switch (item) {
            case "my":
            case "about":
                return <div class={style.linkbox}>
                    <div class={style.link}><strong>live IDE</strong>: sdk.iotdatabox.com</div>
                    <div class={style.link}><strong>github</strong>: github.com/me-box/platform-sdk</div>
                    <div class={style.link}><strong>project site</strong>: databoxproject.uk</div>
                </div>
            default:
                return null;
        }
    }

    render({ item }) {
        const containerstyle = {
            background: item==="design" ? "lightgrey" : "white"
        } 

        return (
            <div>
                <div>
                    <div class={style.summary}>
                        <div class={style.title}>databox</div>
                        <div class={style.datecontainer}>
                            <div class={style.date}>
                                2015-PRESENT
                            </div>
                        </div>

                        <div class={style.projectpic}><ProjectPic /></div>

                    </div>
                    <div class={style.linkbox}>
                        <div class={style.weblabel}>code</div>
                        <div class={style.web}>github.com/me-box/platform-sdk</div>
                        <div class={style.weblabel}>IDE</div>
                        <div class={style.web}>sdk.iotdatabox.com</div>
                        <div class={style.weblabel}>project site</div>
                        <div class={style.web}>www.databoxproject.uk</div>
                    </div>
                </div>
                <div class={style.contentcontainer} style={containerstyle}>
                    <div class={style.menu}>
                        <Link activeClassName={style.active} class={style.back} href="/sc">←</Link>
                        <nav>
                            <Link activeClassName={style.active} href="/sc/project/databox">about</Link>
                            <Link activeClassName={style.active} href="/sc/project/databox/my">my contribution</Link>
                            <Link activeClassName={style.active} href="/sc/project/databox/tech">tech stack</Link>
                            <Link activeClassName={style.active} href="/sc/project/databox/papers">papers</Link>
                            <Link activeClassName={style.active} href="/sc/project/databox/design">design</Link>
                        </nav>
                    </div>
                    <div class={style.content}>


                        <div class={style.maincopy}>
                            {this.renderContent(item)}
                        </div>
                        {this.renderLinks(item)}
                        {this.renderFooterImage(item)}

                    </div>
                </div>
            </div>
        )
    }
}



