import style from '../style';
const Tech = () => (
    <div class={style.tech}>
        <div>
            <div class={style.techtitle}>backend</div>
            <div class={style.title}>linux</div>
            <div class="description">The databox platform runs on osx, linux and raspbian.</div>
            <div class={style.title}>nodejs</div>
            <div class="description">All apps built by the IDE have a nodejs server component.  In test mode this component serves mock IoT data over a websocket.   In production, nodejs communicates with the databox, which provides managed access to live data.</div>
            <div class={style.title}>docker</div>
            <div class="description">All databox components, including apps run in docker containers.  The IDE autogenerates dockerised apps which it pushes to a local docker repository.</div>
            <div class={style.title}>python</div>
            <div class="description">A few IDE nodes communicate with python to get access to machine learning libraries to do, for example, face recognition</div>
        </div>
        <div>
            <div class={style.techtitle}>frontend</div>
            <div class={style.title}>react/redux</div>
            <div class="description">The IDE frontend is written in React and uses Redux for state management.  There
is support for dynamic loading of new IDE nodes which automatically register their reducers with the pre-compiled client code</div>
            <div class={style.title}>flow</div>
            <div class="description">The IDE uses flow in the browser to do realtime type checking of dynamic code.</div>
        </div>
    </div>
);
export default Tech


